<template>
  <div class="boss">
    <ohyeah>
      <Header />
      <div class="home">
        <div class="box"
             :style="`width:${boxWidth}px;height:${boxHeight}px`">
          <ohyeah ref="ohyeah"
                  :no-ver="isNoVer"
                  :no-hor="isNoHor"
                  :autoHide="autoHide"
                  :top="top"
                  :left="left"
                  :thumbColor="thumbColor"
                  :trackColor="trackColor"
                  @onVerStart="onVerStart"
                  @onVerEnd="onVerEnd"
                  @onHorStart="onHorStart"
                  @onHorEnd="onHorEnd"
                  @onScroll="onScroll">
            <ul class="ul-box">
              <li v-for="(item,index) in showData"
                  :key="`${item}-${index}`">{{index+1}}. {{item}}</li>
            </ul>
          </ohyeah>
          <div class="info-w">w:{{boxWidth}}px</div>
          <div class="info-h">h:{{boxHeight}}px</div>
        </div>
        <div class="right">
          <ul>
            <li>offsetHeight: {{info.offsetHeight}}</li>
            <li>offsetWidth: {{info.offsetWidth}}</li>
            <li> height: {{info.height}}</li>
            <li> width: {{info.width}}</li>
            <li>scrollTop:{{info.scrollTop}}</li>
            <li> scrollLeft: {{info.scrollLeft}}</li>
          </ul>
        </div>
      </div>
      <div class="control-box">
        <a-button type="primary"
                  ghost
                  @click="add10">+10条</a-button>
        <a-button type="primary"
                  ghost
                  @click="sub10">-10条</a-button>
        <a-button type="primary"
                  ghost
                  @click="scrollToStart">移动到顶部</a-button>
        <a-button type="primary"
                  ghost
                  @click="scrollToEnd">移动到底部</a-button>
        <a-button type="primary"
                  ghost
                  @click="isNoHor = !isNoHor">{{isNoHor ? '启用水平滚动条' : '禁用水平滚动条'}}</a-button>
        <a-button type="primary"
                  ghost
                  @click="isNoVer = !isNoVer">{{isNoVer ? '启用垂直滚动条' : '禁用垂直滚动条'}}</a-button>
        <a-button type="primary"
                  ghost
                  @click="autoHide = !autoHide">自动隐藏:{{String(autoHide)}}</a-button>
        <a-button type="primary"
                  ghost
                  @click="changeWeizhi">调位置</a-button>
        <a-button type="primary"
                  ghost
                  @click="changeColor">随机换颜色</a-button>
        <a-button type="primary"
                  ghost
                  @click="realColor">还原颜色</a-button>
        <a-button type="primary"
                  ghost
                  @click="getScrollInfo">手动获取一次滚动条各项信息</a-button>
        <a-button type="primary"
                  ghost
                  @click="changeSize">随机改变容器大小</a-button>
      </div>
      <hr />
      <div class="page2">
        <div class="title">嵌套</div>
        <div class="page2-box">
          <ohyeah :autoHide="false">
            <div class="a"
                 @wheel.stop>
              <p>原生滚动条嵌套，需要阻止一下冒泡: @wheel.stop</p>
            </div>
            <div class="b">
              <ohyeah :autoHide="false">
                <p>ohyeah跟ohyeah嵌套</p>
              </ohyeah>
            </div>
          </ohyeah>
        </div>
      </div>
      <a href="https://isluo.com/kernel/index/img/welcome/girl.png"
         download="download.png"
         @click="onClick($event)">aaa</a>
      <Footer />
    </ohyeah>
  </div>

</template>

<script>
// import { OhyeahScroll } from "vue-ohyeah-scroll";
import Header from "@/components/Header";
import Footer from "@/components/Footer";
export default {
  name: "home",
  data() {
    return {
      num: 15,
      showData: [],
      info: {},
      isNoVer: false,
      isNoHor: false,
      autoHide: false,
      top: false,
      left: false,
      boxWidth: 350,
      boxHeight: 500,
      trackColor: "transparent",
      thumbColor: "#7f7f7f",
      source: [
        "这条小路一直绵延到视力不能及的高处",
        "从山脚的这个位置向上望去尽看不到天穹",
        "这山不知几千万里，亦不知其尽头何处",
        "此刻，",
        "他矗立在山口这条被积雪覆盖的狭窄小道起点",
        "陡峭蜿蜒的台阶是继续前行的唯一道路",
        "风如利刃往身体里嵌,刺骨的凛冽",
        "暴雪如鹅毛般在呼啸声中飞舞，白茫茫一片",
        "山峰好似巨人的脊背，坚硬又严肃",
        "最深沉的孤独和绝望扑面而来，他无法动弹，脚下像穿了一双注铅的铁靴",
        "眼神如结冰的湖面",
        "泪痕就是那冰上的裂隙",
        "寒冷在他血液里乱窜",
        "他就站在那里任凭时间向后飞逝",
        "一点点撕碎自己的脆弱和胆怯",
        "他在自己思想斗争的折磨中等待",
        "他的心智早已在漆黑中破灭",
        "或许终有一天潜意识里的某种力量会迸发出瞬息的淡蓝火星",
        "彼时，",
        "他将抓住机会迈出第一步",
        "亦或许他始终无法在梦魇中醒来，又陷入更深的梦境",
        "他曾经从寒冰流域逆行而上",
        "穿越那片由冰川统治的白色地狱",
        "他忘记了自己的名字",
        "脸庞如垂死的树皮",
        "僵硬到没有任何表情",
        "这具身体不是他",
        "他只是在控制着这骨骼和血肉筑成的躯干",
        "他需要依附于这肉身上才能得以前进",
        "行走了如此遥远的路程",
        "灵魂怕是早已在贫瘠的冻原中死了千百会",
        "不知他以何种信念为食",
        "竟能抵达这雪山的脚下"
      ]
    };
  },
  mounted() {
    this.showData.push(...this.source.slice(0, this.num));
    this.info = this.$refs.ohyeah.getScrollInfo();
  },
  components: {
    // OhyeahScroll,
    Header,
    Footer
  },
  methods: {
    add10() {
      for (let i = 0; i < 10; i++) {
        this.showData.push(this.source[this.num]);
        this.num += 1;
        if (this.num >= this.source.length) {
          this.num = 0;
        }
      }

      setTimeout(() => {
        this.info = this.$refs.ohyeah.getScrollInfo();
      }, 16);
    },
    sub10() {
      this.showData.splice(-10, 10);
      setTimeout(() => {
        this.info = this.$refs.ohyeah.getScrollInfo();
      }, 16);
    },
    onScroll(obj) {
      this.info = obj;
    },
    scrollToStart() {
      this.$refs.ohyeah.scrollTo(0, 0, 300);
    },
    scrollToEnd() {
      this.$refs.ohyeah.scrollTo("end", "end", 300);
    },
    noVer() {
      this.isNoVer = true;
    },
    changeWeizhi() {
      this.top = !this.top;
      this.left = !this.left;
    },
    changeColor() {
      this.thumbColor = `rgb(${this.random(0, 255)},${this.random(
        0,
        255
      )},${this.random(0, 255)})`;
      this.trackColor = `rgb(${this.random(0, 255)},${this.random(
        0,
        255
      )},${this.random(0, 255)})`;
    },
    random(min, max) {
      return Math.random() * (max - min) + min;
    },
    realColor() {
      this.thumbColor = "#7f7f7f";
      this.trackColor = "transparent";
    },
    getScrollInfo() {
      setTimeout(() => {
        this.info = this.$refs.ohyeah.getScrollInfo();
        this.$message.success(h => {
          return h("p", null, JSON.stringify(this.info, null, 4));
        });
      }, 16);
    },
    onVerStart() {
      this.$message.info("到顶了");
    },
    onVerEnd() {
      this.$message.info("到底了");
    },
    onHorStart() {
      this.$message.info("到左边了");
    },
    onHorEnd() {
      this.$message.info("到右边了");
    },
    changeSize() {
      this.boxWidth = this.random(200, 500) | 0;
      this.boxHeight = this.random(200, 600) | 0;
    },
    onClick(e) {
      e.preventDefault();
      var link = document.createElement("a");
      link.href = e.target.href;
      link.download = "Download.jpg";
      document.body.appendChild(link);
      link.click();

      //  someFunction(element.href);
      //this.saveImageAs1(e.target.download, e.target.href);
      return false; // prevent default action and stop event propagation
    }
  }
};
</script>

